import { DatePipe, NgClass } from '@angular/common';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import { updateUserPermissions, User, UserIp, UserLoginAttempt } from '@biz-nest/auth-client';
import {
  FrontendLogger,
  getBizNestErrorResponseMessage, IpermProp, Iuser
} from '@biz-nest/core-client';
import {
  MainBarTitleService,
  scrollToTop,
  WindowService
} from '@front-shared-lib';
import { EagleInfoClientError, Imanager } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import {
  SuperPermissionsComponent
} from '../../../../../../../shared-lib/src/lib/components/profile/super-permissions/super-permissions.component';
import {
  AuditLogGroupComponent
} from '../../track/audit-log/audit-log-group/audit-log-group.component';

@Component({
  selector: 'app-manage-user-profile-core',
  standalone: true,
  imports: [
    RouterModule,
    DatePipe,
    SuperPermissionsComponent,
    AuditLogGroupComponent,
    NgClass
  ],
  templateUrl: './manage-user-profile-core.component.html',
  styleUrl: './manage-user-profile-core.component.scss'
})
export class ManageUserProfileCoreComponent
implements OnInit, OnDestroy {
  @Input() user?: Imanager | User | Iuser;
  @Input() authUser?: string | number;
  @Input() canInvite?: boolean;
  @Input() inviting?: boolean;
  @Output() inviteUser = new EventEmitter<undefined >();
  cleanup?: () => void;
  loading = false;
  userLoginAttempt?: UserLoginAttempt;
  userIp?: UserIp;


  constructor(
    private windowService: WindowService,
    private toastrService: ToastrService,
    public mainBarTitle: MainBarTitleService,
    private router: Router
  ) {}

  ngOnInit(): void {
    scrollToTop(this.windowService);
    if (!this.authUser) {
      this.getUser();
    }
  }

  async getUser() {
    if (!this.authUser) {
      return;
    }

    try {
      this.loading = true;
      const user = await User.getOne(this.authUser?.toString() || '');

      if (user) {
        this.user = user;
        this.getLastLogin();
        this.getUserIpRecored();
      }
    } catch (error) {
      FrontendLogger.error(error);
      this.toastrService.error(getBizNestErrorResponseMessage(error), 'Error');
    } finally {
      this.loading = false;
    }
  }

  onPermissionsChange(ev: {[key: string]: boolean | IpermProp} | undefined) {
    const permissions = ev;

    if (!permissions) {
      return;
    }

    this.updatePermissions(permissions);
  }

  async updatePermissions(permissions: {[key: string]: boolean | IpermProp}) {
    if (!this.authUser) {
      throw new EagleInfoClientError('no user found');
    }

    try {
      const userId = this.authUser?.toString() || '';

      await updateUserPermissions(userId, permissions);
    } catch (error) {
      FrontendLogger.error(error);
      this.toastrService.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }

  async updateUser(data: Partial<Iuser>) {
    if (!this.authUser) {
      throw new EagleInfoClientError('no user found');
    }

    try {
      const update = await User.updateOne(this.authUser?.toString() || '', data);

      if (update) {
        this.getUser();
      }
    } catch (error) {
      FrontendLogger.error(error);
      this.toastrService.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }

  verifyUnverifyUser(val: boolean) {
    this.updateUser({ verified: val });
  }

  determineUrl() {
    if (!this.user) {
      return 'assets/imgs/person.png';
    }

    if (this.user.profilePicture) {
      return this.user.profilePicture.url;
    }
  }

  async getLastLogin() {
    if (!this.authUser) {
      return;
    }

    try {
      const { dataArr } = await UserLoginAttempt.filterAll({
        propFilter: {
          user: this.authUser
        },
        propSort: {
          createdAt: 'desc'
        }
      });

      if (dataArr?.length > 0) {
        this.userLoginAttempt = dataArr[0];
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getUserIpRecored() {
    if (!this.authUser) {
      return;
    }

    try {
      const { dataArr } = await UserIp.filterAll({
        propFilter: {
          user: this.authUser
        }
      });

      if (dataArr?.length > 0) {
        this.userIp = dataArr[0];
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async blockUnblockUser(userIp: UserIp, block: boolean) {
    if (!this.userIp) {
      throw new EagleInfoClientError('NO USER IP');
    }

    try {
      const blocked = {
        status: block,
        loginAttemptRef: '',
        timesBlocked: block ? 1 : 0
      };

      await userIp.update({ blocked });
    } catch (error) {
      FrontendLogger.error(error);
      this.toastrService.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }

  viewUserLogingHist() {
    if (!this.authUser) {
      throw new EagleInfoClientError('NO USER');
    }
    this.router.navigate(['/login-history/' + this.authUser]);
  }

  editUser() {
  }

  isVerified() {
    return (this.user as User)?.verified;
  }

  emitInvite() {
    this.inviteUser.emit();
  }


  ngOnDestroy(): void {
    this.cleanup?.();
  }
}
